          }
          if (!this.formData.deliveryAddress) {
            uni.showToast({
              title: "ËØ∑ËæìÂÖ•Êî∂Ë¥ßÂú∞ÂùÄ",
              icon: "none",
            });
            return false;
          }
        }
      }

      return true;
    },



    // Êó•ÊúüÂèòÂåñ
    onDateChange(e) {
      this.formData.appointmentDate = e.detail.value;
    },

    // ÂêåÊÑèÊù°Ê¨æÂèòÂåñ
    onAgreeChange(e) {
      this.formData.agreeTerms = e.detail.value.length > 0;
    },

    // Êó∂Èó¥ÊÆµÂèòÂåñ
    onTimeSlotChange(e) {
      this.formData.appointmentTime = this.timeSlots[e.detail.value];
    },

    // ‰∏ä‰º†ÂõæÁâá
    uploadImage(field) {
      const that = this;
      uni.chooseImage({
        count: 1,
        success(res) {
          const tempFilePath = res.tempFilePaths[0];

          // ÊòæÁ§∫‰∏ä‰º†‰∏≠
          uni.showLoading({
            title: "‰∏ä‰º†‰∏≠...",
          });

          // ËÆæÁΩÆÊú¨Âú∞È¢ÑËßà
          that.formData[field] = tempFilePath;

          // ‰∏ä‰º†Âà∞‰∫ëÂ≠òÂÇ®
          const cloudPath = `idcard/${field}_${new Date().getTime()}${
            tempFilePath.match(/\.[^.]+?$/)[0]
          }`;

          wx.cloud.uploadFile({
            cloudPath: cloudPath,
            filePath: tempFilePath,
            success: (res) => {
              console.log("‰∏ä‰º†ÊàêÂäü", res);
              // Êõ¥Êñ∞‰∏∫‰∫ëÂ≠òÂÇ®Ë∑ØÂæÑ
              that.formData[field + "Cloud"] = res.fileID;
              uni.hideLoading();
              uni.showToast({
                title: "‰∏ä‰º†ÊàêÂäü",
                icon: "success",
              });
            },
            fail: (err) => {
              console.error("‰∏ä‰º†Â§±Ë¥•", err);
              uni.hideLoading();
              uni.showToast({
                title: "‰∏ä‰º†Â§±Ë¥•ÔºåËØ∑ÈáçËØï",
                icon: "none",
              });
            },
          });
        },
      });
    },

    // ÈÄâÊã©ÂæÆ‰ø°Âú∞ÂùÄ
    chooseWxAddress() {
      const that = this;
      // Ë∞ÉÁî®ÂæÆ‰ø°Âú∞ÂùÄÈÄâÊã©Âô®
      uni.chooseAddress({
        success(res) {
          console.log('Ëé∑ÂèñÂæÆ‰ø°Âú∞ÂùÄÊàêÂäü', res);
          // ÊãºÊé•ÂÆåÊï¥Âú∞ÂùÄ
          const fullAddress = `${res.provinceName}${res.cityName}${res.countyName}${res.detailInfo}`;
          
          // Êõ¥Êñ∞Ë°®ÂçïÊï∞ÊçÆ
          that.formData.receiver = res.userName;
          that.formData.contactPhone = res.telNumber;
          that.formData.deliveryAddress = fullAddress;
          
          uni.showToast({
            title: 'Âú∞ÂùÄÂØºÂÖ•ÊàêÂäü',
            icon: 'success'
          });
        },
        fail(err) {
          console.error('Ëé∑ÂèñÂú∞ÂùÄÂ§±Ë¥•', err);
          if (err.errMsg.indexOf('auth deny') > -1) {
            uni.showModal({
              title: 'ÊèêÁ§∫',
              content: 'ËØ∑Âú®ÂæÆ‰ø°ËÆæÁΩÆ‰∏≠ÂÖÅËÆ∏Â∞èÁ®ãÂ∫èËÆøÈóÆÈÄöËÆØÂú∞ÂùÄ',
              showCancel: false
            });
          } else {
            uni.showToast({
              title: 'Ëé∑ÂèñÂú∞ÂùÄÂ§±Ë¥•',
              icon: 'none'
            });
          }
        }
      });
    },

    // Ëé∑ÂèñÁî®Êà∑‰ø°ÊÅØ
    async getUserInfo() {
      return new Promise((resolve) => {
        // Ëé∑ÂèñÂæÆ‰ø°Áî®Êà∑‰ø°ÊÅØ
        uni.getUserInfo({
          success: (res) => {
            resolve(res.userInfo);
          },
          fail: () => {
            // Â¶ÇÊûúËé∑ÂèñÂ§±Ë¥•ÔºåËøîÂõûÁ©∫ÂØπË±°
            resolve({});
          },
        });
      });
    },
  },
};
</script>

